import DescriptionIcon from '@mui/icons-material/Description';
import EditIcon from '@mui/icons-material/Edit';
import DetailsIcon from '@mui/icons-material/Info';
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import ReplayIcon from '@mui/icons-material/Replay';
import SearchIcon from '@mui/icons-material/Search';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import FormControl from '@mui/material/FormControl';
import Grid from '@mui/material/Grid';
import IconButton from '@mui/material/IconButton';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import { DataGrid } from '@mui/x-data-grid';
import { DatePicker } from '@mui/x-date-pickers';
import ReceptionModal from '../components/ReceptionModal';
import modalControl from 'app/modal';
import MUIGridPagination from 'common/components/MUIGridPagination';
import { DEFAULT_PAGINATION } from 'common/constants';
import { dataGrid } from 'common/utils';
import PageContent from 'modules/@core/components/Layout/PageContent';
import { ReceptionPatientModel } from 'services/reception';
import { receptionPatients } from 'test/dummy-data/reception';

interface ExtraApiType {
  onUpdate: (data: ReceptionPatientModel) => void;
  onReception: (data: ReceptionPatientModel) => void;
  onDetails: (data: ReceptionPatientModel) => void;
}

const columnsWithExtraApi = dataGrid.createColumnsWithExtraApi<
  ExtraApiType,
  ReceptionPatientModel
>(extraApi => [
  {
    field: 'patient_code',
    headerName: 'Mã y tế',
    disableColumnMenu: true,
    sortable: false,
    width: 120,
    headerAlign: 'center',
    align: 'center',
  },
  {
    field: 'reception_code',
    headerName: 'Mã nhập viện',
    disableColumnMenu: true,
    sortable: false,
    width: 125,
    headerAlign: 'center',
    align: 'center',
  },
  {
    field: 'fullname',
    headerName: 'Tên',
    disableColumnMenu: true,
    sortable: false,
    minWidth: 150,
    flex: 1,
    headerAlign: 'center',
    // align: 'center',
  },
  {
    field: 'gender',
    headerName: 'Giới tính',
    disableColumnMenu: true,
    sortable: false,
    width: 100,
    headerAlign: 'center',
    align: 'center',
  },
  {
    field: 'birthday',
    headerName: 'Năm sinh',
    disableColumnMenu: true,
    sortable: false,
    width: 100,
    headerAlign: 'center',
    align: 'center',
  },
  {
    field: 'phone',
    headerName: 'Số điện thoại',
    disableColumnMenu: true,
    sortable: false,
    width: 120,
    headerAlign: 'center',
    align: 'center',
  },
  {
    field: 'bhyt',
    headerName: 'BHYT',
    disableColumnMenu: true,
    sortable: false,
    width: 150,
    headerAlign: 'center',
    // align: 'center',
    renderCell({ row }) {
      return (
        <Box py={1}>
          <Typography fontSize={14} fontWeight="bold">
            {row.bhyt.code}
          </Typography>
          <Typography
            fontSize={12}
          >{`Từ ngày: ${row.bhyt.start_date}`}</Typography>
          <Typography
            fontSize={12}
          >{`Đến ngày: ${row.bhyt.end_date}`}</Typography>
        </Box>
      );
    },
  },
  {
    field: 'queue_number',
    headerName: 'STT',
    disableColumnMenu: true,
    sortable: false,
    width: 100,
    headerAlign: 'center',
    align: 'center',
  },
  {
    field: 'room',
    headerName: 'Phòng khám',
    disableColumnMenu: true,
    sortable: false,
    minWidth: 150,
    flex: 1,
    headerAlign: 'center',
    // align: 'center',
  },
  {
    field: 'register_from',
    headerName: 'Đăng ký từ',
    disableColumnMenu: true,
    sortable: false,
    width: 110,
    headerAlign: 'center',
    align: 'center',
  },
  {
    field: 'has_profile',
    headerName: 'Hồ sơ giấy',
    disableColumnMenu: true,
    sortable: false,
    width: 110,
    headerAlign: 'center',
    align: 'center',
    renderCell({ row }) {
      return row.has_profile ? 'Đã có' : 'Chưa có';
    },
  },
  {
    field: 'has_payment',
    headerName: 'Thanh toán tạm ứng',
    disableColumnMenu: true,
    sortable: false,
    width: 180,
    headerAlign: 'center',
    align: 'center',
    renderCell({ row }) {
      return row.has_payment ? 'Đã thanh toán' : 'Chưa thanh toán';
    },
  },
  {
    field: 'actions',
    headerName: '',
    disableColumnMenu: true,
    sortable: false,
    minWidth: 150,
    flex: 1,
    headerAlign: 'center',
    align: 'center',
    renderCell({ row }) {
      return (
        <Stack direction="row" spacing={1} flex={1} justifyContent="flex-end">
          <IconButton
            aria-label="Chỉnh sửa"
            onClick={() => extraApi.onUpdate(row)}
          >
            <EditIcon />
          </IconButton>
          <IconButton
            aria-label="Tiếp nhận"
            onClick={() => extraApi.onReception(row)}
          >
            <PlayArrowIcon />
          </IconButton>
          <IconButton
            aria-label="Xem chi tiết"
            onClick={() => extraApi.onDetails(row)}
          >
            <DetailsIcon />
          </IconButton>
        </Stack>
      );
    },
  },
]);

export default function ReceptionPatientsPage() {
  // Modal
  const modal = modalControl.useModalComponentControl(
    ReceptionModal,
    undefined,
    {
      maxWidth: 'lg',
      title: 'Quản lý tiếp nhận, đăng ký khám chữa bệnh',
    },
  );

  // Table
  const columns = columnsWithExtraApi.useToColumns(() => ({
    onUpdate(data) {
      modal.open();
    },
    onReception(data) {
      modal.open();
    },
    onDetails(data) {
      modal.open();
    },
  }));

  // Render
  return (
    <PageContent display="flex" flexDirection="column" height="100%">
      <Grid container spacing={1.5}>
        <Grid item xs={12} md={3} lg={2}>
          <TextField size="small" label="Mã y tế" fullWidth />
        </Grid>
        <Grid item xs={12} md={3} lg={2}>
          <TextField size="small" label="STT" fullWidth />
        </Grid>
        <Grid item xs={12} md={3} lg={2}>
          <DatePicker
            slotProps={{
              textField: {
                size: 'small',
                fullWidth: true,
              },
            }}
            label="Ngày"
          />
        </Grid>
        <Grid item xs={12} md={3} lg={2}>
          <FormControl fullWidth size="small">
            <InputLabel id="status-label">Trạng thái</InputLabel>
            <Select labelId="status-label" label="Trạng thái" defaultValue="">
              <MenuItem value={10}>Chờ tiếp nhận</MenuItem>
              <MenuItem value={20}>Đã tiếp nhận</MenuItem>
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={12} md={3} lg={2}>
          <Button size="medium" startIcon={<SearchIcon />} variant="contained">
            Tìm kiếm
          </Button>
        </Grid>
      </Grid>
      <Grid container spacing={1.5} marginTop={2}>
        <Grid item xs={12} md={6}>
          <Typography variant="h5">DANH SÁCH TIẾP NHẬN BỆNH NHÂN</Typography>
        </Grid>
        <Grid item xs={12} md={6} textAlign="end">
          <Button size="medium" startIcon={<ReplayIcon />} variant="outlined">
            Làm mới
          </Button>
          <Button
            size="medium"
            startIcon={<DescriptionIcon />}
            variant="outlined"
            sx={{
              ml: 2,
            }}
          >
            Xuất Excel
          </Button>
        </Grid>
      </Grid>
      <DataGrid
        sx={{
          marginTop: 1,
          flex: 1,
        }}
        columns={columns}
        rows={receptionPatients}
        disableRowSelectionOnClick
        getRowHeight={() => 'auto'}
        // disableVirtualization
        slots={{
          pagination: MUIGridPagination,
        }}
        slotProps={{
          pagination: {
            count: receptionPatients.length,
            rowsPerPageOptions:
              DEFAULT_PAGINATION.PAGE_SIZE_OPTIONS as unknown as number[],
          },
        }}
      />
    </PageContent>
  );
}
